import { DomStuff } from '../domStuff/domStuff';

export class SidebarCategory {
  constructor(name, displayName, contents, canAdd, icon = '') {
    this.name = name;
    this.displayName = displayName;
    this.contents = contents;
    this.icon = icon;
    this.canAdd = canAdd;

    const button = DomStuff.makeButton(icon + ' ' + displayName);
    button.classList.add('categoryButton');
    this.button = button;

    this.button.setAttribute('buttonName', name);
  }

  getCanAdd() {
    return this.canAdd;
  }

  getName() {
    return this.name;
  }

  getIcon() {
    return this.icon;
  }

  getDisplayName() {
    return this.displayName;
  }

  setName(name) {
    this.name = name;
  }

  setDisplayName(name) {
    this.displayName = name;
  }

  getContents() {
    return this.contents;
  }

  setContents(contents) {
    this.contents = contents;
  }

  getButton() {
    return this.button;
  }
}
