___CSS_LOADER_EXPORT___.push([module.id, `#sidebar {
  display: flex;
  flex-direction: column;

  border: 3px solid var(--strokeColor);
  border-radius: 12px;

  flex: 1;
  max-height: calc(100vh - 2em);

  margin: 1em;

  box-shadow: 0px 10px 20px 4px var(--boxShadow);

  background-color: var(--bgColor);
  transition: 0.25s;
}

.nowrap {
  text-wrap-mode: nowrap;
}

#sidebar > * > button {
  color: var(--textColor);
  background-color: var(--buttonBg);
}

#sidebar > * > button.clicked {
  background-color: var(--buttonBgClicked);
}

.sidebarSection {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: stretch;

  gap: 1em;

  padding: 2em 1em;
}

#basics {
  border-bottom: 3px solid var(--strokeColor);
}

#sidebar > * > button {
  font-size: 1.5rem;
  border-radius: 8px;
  width: 100%;
}

#sidebar button:hover {
  background: var(--buttonBgHover);
}

.categoryForm {
  border: 3px solid var(--strokeColor);
  border-radius: 6px;
  padding: 1em;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  gap: 8px;
}

.categoryForm > input[type='text'] {
  text-align: center;
}

#userCategories {
  overflow-x: hidden;
  overflow-y: auto;
  scrollbar-gutter: stable;
  scrollbar-width: 4px;
  padding-right: calc(1em - 8px);
}

#userCategories::-webkit-scrollbar {
  width: 8px;
  background-color: transparent;
}

#userCategories::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 4px;
  opacity: 0.7;
}
